import React from 'react';
import axios from 'axios';
import styles from  '../css/login.css';
import { Nav, Navbar, Form, FormControl,NavDropdown } from 'react-bootstrap';
import { Dropdown,Icon,Button,Card,Divider,Segment,Checkbox, Input, Label,Loading,Loader, Dimmer,List,Table, Container, Modal } from 'semantic-ui-react';



class Board extends React.Component {
    
    constructor() {
        super();
        this.state = {
            user:this.props,
            classes: null
        }
        
    }

    async getData(user_id){
       
        axios.get('https://maxwelljonesdesign.com/classes/'+user_id )
        .then(res => {
          if(res.data){
            this.setState({classes: res.data});
            
          }
          else{
           
          }
      })
    }
    componentDidMount() {
        this.getData(this.props.user.id);
    }

    logout(){
        console.log("logging out");
        localStorage.setItem("user",null);
        this.setState({user: null});
        window.location.reload(true);
    }

    createClass(){
        var name = prompt("Please enter the name of the class");
        var prof = prompt("Please enter the prof of the class");

        let classObject = {
            user_id:this.props.user.id,
            name:name,
            prof:prof
        }
        axios.post('https://maxwelljonesdesign.com/classes/create',classObject )
        .then(res => {
          if(res.data){
              console.log("success");
              window.location.reload(true);
          }
          else{
            console.log("fail");
          }
      })
    }

    deleteClass(){
        var id = prompt("Please enter the id: ");
        axios.get('https://maxwelljonesdesign.com/classes/delete/'+id)
        .then(res => {
            if (res.status === 200) {
                console.log("success");
              window.location.reload(true);
            }
          else{
            console.log("fail");
          }
      })
    }

    classesComponent(){
        return(
            <div style={{width:'100%',overflowY:'auto',height:'93vh',padding:'4vh'}}>

                <Card id='cardMain'>
                    <h2 style={{margin:'0px'}}>Classes</h2>
                    <div style={{padding:'2vw'}}>
                    {this.state.classes.map(item =>
                        <li key={item.id}>{item.id} - {item.name} ~ {item.prof}</li>
                    )}
                    </div>

                </Card>
                <Card id='cardMain'>
                    <h2 style={{margin:'0px'}}>Classes</h2>
                    <div style={{padding:'2vw'}}>
                    {this.state.classes.map(item =>
                        <li key={item.id}>{item.id} - {item.name} ~ {item.prof}</li>
                    )}
                    </div>

                </Card>
                <Card id='cardMain'>
                    <h2 style={{margin:'0px'}}>Classes</h2>
                    <div style={{padding:'2vw'}}>
                    {this.state.classes.map(item =>
                        <li key={item.id}>{item.id} - {item.name} ~ {item.prof}</li>
                    )}
                    </div>

                </Card>
                

                <div style={{margin:'5px',textAlign:'center',marginBottom:'60px'}}>
                    <p style={{textAlign:'center',color:'white',padding:'0px'}}><a style={{color:'white',opacity:'50%'}} href = "mailto:maxjones2001@hotmail.com?subject=Help">Need help?</a></p>
                </div>
            </div>
        );
    }

    render() {
        
        return(
            <div id='noscroll'>
                <div style={{width:'100%',padding:'.75rem',height:'7vh',backgroundColor:'rgba(33,43,78,255)'}}>
                    <Button onClick={()=>this.logout()} style={{margin:'auto',textAlign:'center',backgroundColor:'rgba(33,53,108,255)',color:'white',marginRight:'5px'}}>Logout
                    </Button>
                    <Button style={{backgroundColor:'rgba(33,53,108,255)',color:'white'}}>
                        <Dropdown text='Classes' pointing className='link item'>
                            <Dropdown.Menu>
                                <Dropdown.Header>Categories</Dropdown.Header>
                                <Dropdown.Item onClick={()=>this.createClass()}>Create</Dropdown.Item>
                                <Dropdown.Item onClick={()=>this.deleteClass()}>Delete</Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    </Button>
                    
                </div>
                {this.state.classes && this.classesComponent()}
               
            </div>
        );
    }      
}

export default Board;